/* GENERAL */

#top {
  padding: 100px;
  padding-top: 100px;
  padding-bottom: 0px;
  width: 1000px;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;

  position: relative;
}

#topname {
  width: 1200px;
  padding-left: 120px;
}

h1,
h2,
p,
label {
  font-family: Helvetica Neue, Arial, sans-serif;
  color: white;
}

#marketing,
#design,
#about {
  position: relative;
  padding-top: 250px;
}

#marketingheading,
#designheading,
#aboutheading {
  font-size: 125px;
  position: absolute;
  top: 60px;
  left: 150px;
  color: black;
  transition: transform 500ms;
  z-index: 1;
}

#marketing:hover > #marketingheading {
  transform: translateY(-25px);
  transition: all 1s ease 0s;
}

#design:hover > #designheading {
  transform: translateY(-25px);
  transition: all 1s ease 0s;
}

#about:hover > #aboutheading {
  transform: translateY(-25px);
  transition: all 1s ease 0s;
}

