/* NAVBAR */
#nav {
  width: auto;
  margin: auto;
  padding-top: 14px;
  padding-bottom: 6px;
  height: 55px;
}

#navbar {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 10;
}

#nav ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

#nav li {
  width: 125px;
  font-size: 18px;
  font-family: Helvetica Neue, Arial, sans-serif;
  color: white;
  text-align: center;
}

#nav > ul {
  display: flex;
  flex-direction: row;
}

#nav a {
  display: block;
  text-decoration: none;
  color: white;
  line-height: 36px;
  height: 39px;
  padding: 6px;
}

#nav li:hover {
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0);
}

#nav a:hover {
  color: #034078;
}

#nav ul ul {
  display: none;
}

#nav li:hover > ul {
  display: block;
  background-color: rgba(0, 0, 0, 0.6);
}

.jw {
  height: 30px;
  padding-left: 25px;
}

#navbar {
  position: -webkit-sticky;
  position: sticky;
  position: fixed;
  top: 0;
  height: 65px;
  width: 100%;
  background: linear-gradient(to bottom, #000, #0003 70%, #0000);
  transition: background 0.5s;
}

#navbar.scrolled {
  background: rgba(0, 0, 0, 0.8);
}

